import React from 'react';
import styles from './AboutUs.module.css';
import { FaBrain, FaDna, FaHeartbeat, FaMapMarkerAlt, FaTrophy, FaProjectDiagram } from 'react-icons/fa';
import { Container, Typography, Box, Grid, Card, CardContent, IconButton } from '@mui/material';
import { LocationOn, Email, Phone } from '@mui/icons-material';
import ImageSlider from '../../ImageSlider/ImageSlider';
import Hero from '../../Layout/Hero/Hero';
import ReactMarkdown from 'react-markdown';
import { useState, useEffect } from 'react';
import remarkGfm from 'remark-gfm';

function AboutUs() {
  const [markdownContent, setMarkdownContent] = useState(['']);

  // Markdown 파일 로드
  useEffect(() => {
    fetch('/data/introductionDetailed.md')
      .then(res => res.text())
      .then(text => setMarkdownContent(text.split('&분리자&')));
  }, []);

  return (
    <>
      <Hero name="About Us" />
      <section className={styles.introduction}>
        <article className={styles.article}>
          <header className={styles.headerContent}>
            <h1 className={styles.heading}>
              <div className={styles.headingBackground} />
              <span className={styles.welcomeText}>Welcome</span>
            </h1>
            {/* <h2 className={styles.subHeading}>저희 BEAR LAB 방문을 환영합니다.</h2> */}
          </header>

          <div className={styles.description}>
            {markdownContent.map((content, index) =>
              content.startsWith('$$') ? (
                <div className={styles.SliderWrapper}>
                  <ImageSlider className={styles.imageSlider} />
                </div>
              ) : (
                <ReactMarkdown key={index} children={content} remarkPlugins={[remarkGfm]} />
              ),
            )}
          </div>
        </article>
      </section>
    </>
  );
}

export default AboutUs;
