import React, { useEffect } from 'react';
import { useNavigate } from 'react-router-dom';

const AdminRedirect = () => {
  const navigate = useNavigate();

  useEffect(() => {
    // sessionStorage에서 토큰 정보 확인
    const token = sessionStorage.getItem('github_access_token');
    const REDIRECT_URI = 'https://bearailab.github.io/callback';

    if (token) {
      // 토큰이 있으면 /admin 경로로 이동
      navigate('/admin/landing'); // 실제 admin 페이지로 이동
    } else {
      // 토큰이 없으면 GitHub OAuth 페이지로 리다이렉트
      window.location.href = `https://github.com/login/oauth/authorize?client_id=Ov23liZ6tuy0yNyYfbNV&redirect_url=${encodeURIComponent(REDIRECT_URI)}&scope=repo user`;
    }
  }, []);

  return <div>Redirecting...</div>;
};

export default AdminRedirect;
