import React, { useState } from 'react';
import styles from './AdminMenu.module.css';

const MemberEditModal = ({ open, onClose, onSubmit, MemberInfo }) => {
  const [formState, setFormState] = useState(MemberInfo);

  if (!open) return null;

  const handleChange = e => {
    const { name, value } = e.target;
    setFormState(prev => ({
      ...prev,
      [name]: value,
    }));
  };

  const handleSubmit = e => {
    e.preventDefault();
    onSubmit(formState);
  };

  return (
    <div className={styles.modalOverlay}>
      <div className={styles.modalContent}>
        <button className={styles.modalClose} onClick={onClose}>
          ✖
        </button>
        <h2 className={styles.modalHeader}>Edit Member Info</h2>
        <form onSubmit={handleSubmit}>
          <div className={styles.modalField}>
            <label htmlFor="name">Name:</label>
            <input type="text" name="name" value={formState.name} onChange={handleChange} required />
          </div>
          <div className={styles.modalField}>
            <label htmlFor="title">Title:</label>
            <input type="text" name="title" value={formState.title} onChange={handleChange} required />
          </div>
          <div className={styles.modalField}>
            <label htmlFor="imageSrc">Image URL:</label>
            <input type="url" name="imageSrc" value={formState.imageSrc} onChange={handleChange} />
          </div>
          <div className={styles.modalField}>
            <label htmlFor="description">Description:</label>
            <textarea name="description" value={formState.description} onChange={handleChange} rows="3" />
          </div>
          <div className={styles.modalField}>
            <label htmlFor="email">Email:</label>
            <input type="email" name="email" value={formState.email} onChange={handleChange} required />
          </div>
          <div className={styles.modalField}>
            <label htmlFor="cvLink">CV Link:</label>
            <input type="url" name="cvLink" value={formState.cvLink} onChange={handleChange} />
          </div>
          <div className={styles.modalField}>
            <label>Role:</label>
            <div>
              <input
                type="radio"
                name="role"
                value="professor"
                checked={formState.role === 'professor'}
                onChange={handleChange}
              />
              Professor
              <input
                type="radio"
                name="role"
                value="member"
                checked={formState.role === 'member'}
                onChange={handleChange}
              />
              Member
              <input
                type="radio"
                name="role"
                value="student"
                checked={formState.role === 'student'}
                onChange={handleChange}
              />
              Student
              <input
                type="radio"
                name="role"
                value="alumni"
                checked={formState.role === 'alumni'}
                onChange={handleChange}
              />
              Alumni
            </div>
          </div>
          <button type="submit" className={styles.modalSubmit}>
            Save Changes
          </button>
        </form>
      </div>
    </div>
  );
};

export default MemberEditModal;
